export default {
    data() {
        return {
            email: decodeURIComponent(this.$route.query.email),
            password: this.$route.query.code || '',
        }
    },
    methods: {
        submitForm() {
            this.$store.dispatch('auth/loginUser', {
                email: this.email,
                password: this.password,
                endpoint: '/auth/login'
            })
            .then(() => {
                this.$store.dispatch('auth/setUser', {
                    endpoint: '/user'
                })
                this.$router.push('/')
            })
        }
    }
}
